const useCompanyProfile = () => {
  const data = reactive({
    logo: null,
    company_email: null,
    name: null,
    legal_business_name: null,
    email_sender: null,
    address: null,
    address_text: null,
    phone: null,
    city: null,
    state: null,
    zip: null,
    country: null,
    country_name: null,
    country_code: null,
    suite: null,
    lon: null,
    lat: null,
    package_name: null,
    package_status: null,
    total_user: null,
    created_at: null,
    unlinkable: null,
    is_selected: false,
    tag: null
  })
  const dataStoreOpenHours = reactive({
    data: [],
    individual_hours: 0
  })

  const setData = (payload = {}) => {
    data.logo = payload.logo || payload.company_logo || null
    data.company_email = payload.company_email || null
    data.name = payload.name || null
    data.legal_business_name = payload.legal_business_name || null
    data.email_sender = payload.email_sender || null
    data.address = payload.address || null
    data.address_text = payload.address_text || null
    data.phone = payload.phone || null
    data.city = payload.city || null
    data.state = payload.state || null
    data.zip = payload.zip || null
    data.country = payload.country || null
    data.country_name = payload.country_name || null
    data.country_code = payload.country_code || null
    data.suite = payload.suite || ''
    data.lon = payload.lon || null
    data.lat = payload.lat || null
    data.package_name = payload.package_name || null
    data.package_status = payload.package_status || null
    data.total_user = payload.total_user || null
    data.created_at = payload.created_at || null
    data.unlinkable = payload.unlinkable || null
    data.is_selected = payload.is_selected || false
    data.tag = payload.tag || null
  }
  const setState = (data) => {
    state.company_profile.time_format = data.time_format
    state.company_profile.date_format = data.date_format
  }
  const actionShow = async (payload, callback = () => {}) => {
    loading.company_profile.show = true
    const response = await store.dispatch(`setting/companyProfile/show`, {
      ...(payload && payload.is_store && { is_store: payload.is_store })
    })
    setData(response)
    setState(response)
    isFunction(callback) && callback()
    loading.company_profile.show = false
  }
  const showCompanyByToken = async (payload, callback = () => {}) => {
    loading.company_profile.show = true
    const response = await store.dispatch(`setting/companyProfile/showByToken`, {
      ...payload
    })
    setData(response)
    setState(response)
    isFunction(callback) && callback()
    loading.company_profile.show = false
  }
  const actionUpdate = async (callback = () => {}) => {
    try {
      loading.company_profile.update = true
      let formData = new FormData()
      Object.entries(data).forEach(item => {
        if(item[0] === 'logo' && item[1] === null) {
          formData.append(item[0], 0)
          return
        }
        if(item[1] !== null) {
          if(item[0] !== 'logo' || (item[0] === 'logo' && typeof item[1] === 'object')) formData.append(item[0], item[1])
        }
      })
      formData.append('_method', 'PUT')
      await store.dispatch(`setting/companyProfile/showUpdate`, formData)
      await actionShow({ is_store: 1 })
      isFunction(callback) && callback(null, null)
    }
    catch(err){
      isFunction(callback) && callback(err, null)
    }
    finally {
      loading.company_profile.update = false
    }
  }
  const actionShowStoreOpenHours = async(callback = () =>{}) => {
    try {
      loading.company_profile.open_hours.show = true
      const response = await store.dispatch(`setting/companyProfile/showStoreOpenHours`)
      Object.assign(dataStoreOpenHours, response)
      isFunction(callback) && callback(response)
    }
    finally {
      loading.company_profile.open_hours.show = false
    }
  }
  const actionUpdateStoreOpenHours = async(callback = ()=> {}) => {
    try{
      loading.company_profile.open_hours.update = true
      const {data: storeHours, individual_hours} = dataStoreOpenHours
      await Promise.all([
        store.dispatch('setting/companyProfile/updateStoreOpenHours', storeHours),
        store.dispatch('setting/companyProfile/updateStoreOpenHoursIndividual', {
          individual_hours
        })
      ])
      isFunction(callback) && callback()
    }finally{
      loading.company_profile.open_hours.update = false
    }
  }
  const actionCloseCompany = async(callback = () => {}) => {
    try{
      loading.company_profile.closeCompany = true
      const response = await store.dispatch('setting/companyProfile/closeCompany')
      isFunction(callback) && callback(response)
    } finally {
      loading.company_profile.closeCompany = false
    }
  }
  const actionUnlinkAccount = async(payload, callback = () => {}) => {
    try{
      loading.company_profile.unlinkAccount = true
      const response = await store.dispatch('setting/companyProfile/unlinkAccount', {
        ...payload
      })
      isFunction(callback) && callback(response)
    } finally {
      loading.company_profile.unlinkAccount = false
    }
  }
  const companyProfile = computed(() => store.state.setting.companyProfile)

  return {
    data,
    dataStoreOpenHours,
    loading: loading.company_profile,
    actionShow,
    showCompanyByToken,
    actionUpdate,
    companyProfile,
    actionCloseCompany,
    actionShowStoreOpenHours,
    actionUpdateStoreOpenHours,
    actionUnlinkAccount
  }
}

const useManageUserAll = () => {
  const data = reactive({
    page: 1,
    limit: 10,
    search: '',
    sort_by: '',
    order_by: '',
    invited: 0,
    type: 0
  })
  const loading = reactive({
    all: false,
    delete: false
  })
  const modal = reactive({
    delete: false
  })
  const selected = ref(null)
  const unsetData = () => {
    selected.value = null
  }
  const openModal = (target, callback = () => {}) => {
    modal[target] = true
    isFunction(callback) && callback()
  }
  const actionAll = async (callback = () => {}) => {
    loading.all = true
    await store.dispatch('setting/manageUser/all', data)
    isFunction(callback) && callback()
    loading.all = false
  }
  const actionDelete = async (callback = () => {}) => {
    try {
      loading.delete = true
      await store.dispatch(`setting/manageUser/delete`, selected.value)
      isFunction(callback) && callback()
      modal.delete = false
      unsetData()
      await actionAll()
    }
    finally {
      loading.delete = false
    }
  }
  const tabs = computed(() => {
    return [{
      name: 'Member List',
      value: 0,
      total_invited: null
    }, {
      name: 'Invite Sent',
      value: 1,
      total_invited: manageUserState.invite.total_invited || 0
    }, {
      name: 'My Permissions',
      value: 2,
      total_invited: null
    }]
  })
  const state = computed(() => {
    return store.state.setting.manageUser.all
  })

  return {
    data,
    state,
    loading,
    modal,
    selected,
    tabs,
    unsetData,
    openModal,
    actionAll,
    actionDelete
  }
}

const useManageUserForm = () => {
  const data = reactive({
    email: null,
    first_name: null,
    last_name: null,
    job_title: null,
    status: 1,
    role_id: 1,
    password: null,
    default_location_id: null,
    default_location_name: null,
    locations: [],
    location_permission_all: null,
    permission: {
      create: false,
      read: false,
      update: false,
      delete: false
    },
    menu_list: null,
    menus: [],
    // is_view_confidential: 0
  })
  const loading = reactive({
    show: false,
    form: false,
    permission: false
  })
  const setData = (payload = {}) => {
    data.email = payload.email
    data.first_name = payload.first_name
    data.last_name = payload.last_name
    data.job_title = payload.job_title
    data.status = payload.status
    data.default_location_id = payload.default_location_id
    data.default_location_name = payload.default_location_name
    // data.is_view_confidential = payload.is_view_confidential
    data.permission = payload.permission
  }

  const unsetData = () => {
    data.email = null
    data.first_name = null
    data.last_name = null
    data.job_title = null
    data.status = null
    data.password = null
    data.default_location_id = null
  }
  const formatMenus = (menus=[], formattedMenus=[]) => {
    for(let menu of menus){
      if(menu.id) {
        formattedMenus.push({
          id: menu.id,
          order: !menu.order ? 0 : menu.order,
          status: menu.checked === true ? 1 : 0
        })
      }
      const hasSubChild = menu.child.length > 0
      if(hasSubChild) formatMenus(menu.child, formattedMenus)
    }
    return formattedMenus
  }
  const actionShow = async (userId, callback = () => {}) => {
    loading.show = true
    const response = await store.dispatch(`setting/manageUser/show`, userId)
    setData(response)
    isFunction(callback) && callback()
    loading.show = false
  }
  const actionCreate = async (callback = () => {}) => {
    const {menu_list, ...rest} = data
    try {
      loading.form = true
      await store.dispatch(`setting/manageUser/create`, rest)
      isFunction(callback) && callback()
      unsetData()
    }
    finally {
      loading.form = false
    }
  }
  const actionUpdate = async (userId, callback = () => {}) => {
    try {
      loading.form = true
      const formattedMenus = formatMenus(data.menus)
      await Promise.all([
        store.dispatch(`setting/manageUser/update`, {
          id: userId,
          data
        }),
        store.dispatch(`setting/manageUser/permissionUpdate`, {
          id: userId,
          data: {
            menus: formattedMenus
          }
        })
      ])
      isFunction(callback) && callback()
    }
    finally {
      loading.form = false
    }
  }
  // const actionPermission = async (userId, callback = () => {}) => {
  //   loading.permission = true
  //   data.permission = await store.dispatch(`setting/manageUser/permission`, userId).then(response => response.active)
  //   isFunction(callback) && callback()
  //   loading.permission = false
  // }

  const getMenu = async (userId, callback = () => {}) => {
    data.menu_list = await store.dispatch(`setting/manageUser/getMenu`, userId ? userId : '')
    isFunction(callback) && callback()
  }

  return {
    data,
    loading,
    actionShow,
    actionCreate,
    actionUpdate,
    // actionPermission,
    getMenu
  }
}

const useManageUserInvite = () => {
  const unsetInvitationData = () => {
    manageUserState.invite.data = {
      email: [],
      role_id: 1,
      default_location_id: null,
      permission: [],
      menus: [],
      is_view_confidential: 0,
      locations: null,
      location_permission_all: null
    }
  }
  const formatMenus = (menus=[], formattedMenus=[]) => {
    for(let menu of menus){
      if(menu.id) {
        formattedMenus.push({
          id: menu.id,
          order: !menu.order ? 0 : menu.order,
          status: menu.checked === true ? 1 : 0
        })
      }
      const hasSubChild = menu.child.length > 0
      if(hasSubChild) formatMenus(menu.child, formattedMenus)
    }
    return formattedMenus
  }
  const action = async (callback=()=>{}) => {
    try{
      manageUserState.invite.loading.create = true
      const payload = {...manageUserState.invite.data}
      const menus = formatMenus(payload.menus)
      payload.menus = JSON.stringify(menus)
      const response = await store.dispatch('setting/manageUser/inviteAccount', payload)
      isFunction(callback) && callback(response)
      unsetInvitationData()
    }finally{
      manageUserState.invite.loading.create = false
    }
  }
  const actionMenus = async (userId, callback = () => {}) => {
    manageUserState.invite.loading.menus = true
    const menus = await store.dispatch(`setting/manageUser/getMenu`, userId ? userId : '')
    manageUserState.invite.menu_list = {...menus, pii: [{
      id: 1,
      name: 'View Account and Routing Information',
      status: 0
    }]}
    isFunction(callback) && callback()
    manageUserState.invite.loading.menus = false
  }
  const actionResend = async (id, callback=()=>{}) => {
    try{
      manageUserState.invite.loading.resend.push(id)
      await store.dispatch(`setting/manageUser/inviteAccountResend`, id)
      isFunction(callback) && callback()
    }finally{
      const loadingIndex = manageUserState.invite.loading.resend.findIndex(resendId => resendId === id)
      if(loadingIndex !== -1) manageUserState.invite.loading.resend.splice(loadingIndex, 1)
    }
  }
  const actionRevoke = async(id, callback=()=>{}) => {
    try{
      manageUserState.invite.loading.revoke.push(id)
      await store.dispatch(`setting/manageUser/inviteAccountRevoke`, id)
      isFunction(callback) && callback()
    }finally{
      const loadingIndex = manageUserState.invite.loading.revoke.findIndex(revokeId => revokeId === id)
      if(loadingIndex !== -1) manageUserState.invite.loading.revoke.splice(loadingIndex, 1)
    }
  }
  const actionTotalInvited = async (callback=()=>{}) => {
    try{
      manageUserState.invite.loading.total_invited = true
      manageUserState.invite.total_invited = await store.dispatch(`setting/manageUser/inviteTotalInvited`)
      isFunction(callback) && callback()
    }finally{
      manageUserState.invite.loading.total_invited = false
    }
  }

  const actionGetData = async (userId, callback = () => {}) => {
    try {
      manageUserState.invite.loading.data = true;
  
      const [show, locations] = await Promise.all([
        store.dispatch(`setting/manageUser/show`, userId),
        store.dispatch(`setting/manageUser/getLocations`, { params: { admin_id: userId } }),
      ]);
  
      manageUserState.invite.data = { ...show };
      manageUserState.invite.data.locations = locations.selected;
  
      await actionMenus(userId);
  
      isFunction(callback) && callback();
    } catch (error) {
      console.error(error);
    } finally {
      manageUserState.invite.loading.data = false;
    }
  };
  
  
  const actionUpdateInvite = async (callback = () => {}) => {
    const data = {
      ...manageUserState.invite.data
    };
  
    try {
      manageUserState.invite.loading.update = true;
      const [updateResponse, permissionResponse] = await Promise.all([
        store.dispatch(`setting/manageUser/update`, {
          id: manageUserState.invite.selectedId,
          data
        }),
        store.dispatch(`setting/manageUser/permissionUpdate`, {
          id: manageUserState.invite.selectedId,
          data: { menus: formatMenus(manageUserState.invite.data.menus) }
        })
      ]);
  
      if (permissionResponse === 'success') {
        await actionResend(manageUserState.invite.selectedId);
      }
  
      isFunction(callback) && callback([updateResponse, permissionResponse]);
      unsetInvitationData();
    } catch (error) {
      console.error('Error updating invite:', error);
    } finally {
      manageUserState.invite.loading.update = false;
    }
  };
  
  
  return {
    action,
    actionMenus,
    actionResend,
    actionRevoke,
    actionTotalInvited,
    actionGetData,
    actionUpdateInvite
  }
}

