const useBuybackDevice = () => {
  const storePiniaSetting = useSetting()
  const loading = reactive({
    all: false,
    update: false,
    carriers: false
  })
  const data = reactive({
    page: 1,
    limit: 10,
    search: '',
    sort_by: '',
    order_by: ''
  })
  const exportData = reactive({
    format: 'csv',
    show: false,
    loading: false
  })
  const cancelToken = ref(null)

  const actionAll = async (category, brand, callback = () => {}, options={}) => {
    try {
      if(options.loading === undefined || options.loading === true) loading.all = true
  
      const tempData = Object.assign({}, {
        ...data,
        categories: category,
        manufacturer: brand
      })
      await store.dispatch('setting/buybackDevice/all', tempData)
      isFunction(callback) && callback()
      loading.all = false
    } catch (error) {
      loading.all = false
    }
  }
  const actionGetChildren = async (payload, callback = () => {}) => {
    try {
      const response = await store.dispatch('setting/buybackDevice/getChildren', payload)
      return isFunction(callback) && callback(response?.children ?? [])
    } catch (error) {
      return isFunction(callback) && callback([])
    }
  }

  const state = computed(() =>  store.state.setting.buybackDevice.devices)
  const stateAll = computed(() => store.state.setting.buybackDevice.all)
  const categories = computed(() => Array.isArray(store.state.setting.buybackDevice.categories) ? store.state.setting.buybackDevice.categories.map((category) => ({...category, is_book: category.name?.toLowerCase?.() === 'books'}))  : [])
  const brands = computed(() => store.state.setting.buybackDevice.brands)
  const carriers = ref([])

  const categoriesTab = computed(() => categories.value.map(category => ({...category, id: category.name ? category.name.toLowerCase() : ''})).map(category => ({...mapTabRoute(category, 'settingBuybackDeviceTab', {
    category: category.id,
  }, 'id', 'name'), ...category})))
  const loadingTabBrand = ref(false)
  const timerLoad = ref(null)
  const tabBatchSize = 20
  const currentTabOffset = ref(0) // bisa untuk pagination, infinite scroll, dsb

  const brandsTab = computed(() => {
    const brandList = ['All']
    const cleanBrands = brands.value.filter(brand => brand.length)

    let offset = currentTabOffset.value
    let sliced = cleanBrands.slice(offset, offset + tabBatchSize)

    // fallback jika kosong
    if (sliced.length === 0 && offset > 0) {
      offset = Math.max(0, cleanBrands.length - tabBatchSize)
      sliced = cleanBrands.slice(offset, offset + tabBatchSize)
    }

    const allBrands = brandList.concat(sliced)

    return allBrands.map(brand => ({
      name: brand,
      id: brand?.toLowerCase()
    })).map(brand => ({
      ...mapTabRoute(brand, 'settingBuybackDeviceBrand', {
        brand: brand.id
      }, 'id', 'name')
    }))
  })
  const onLoadMoreTabs = () => {
    clearTimeout(timerLoad.value)
    loadingTabBrand.value = true
    setTimeout(() => {
      const total = brands.value.length
      const nextOffset = currentTabOffset.value + tabBatchSize
  
      if (nextOffset < total) {
        currentTabOffset.value = nextOffset
      }
      loadingTabBrand.value = false
    }, 300)
  }

  const actionUpdate = async (data, callback = () => {}, responseOnCallback = false) => {
    try {
      loading.update = true
      const response = await store.dispatch('setting/buybackDevice/update', data)
      await store.dispatch('user/me')
      storePiniaSetting.setMenu(store.state.user.me.menu_v2)

      isFunction(callback) && callback(responseOnCallback ? response : null)
      loading.update = false
      return response
    } catch (error) {
      loading.update = false
      isFunction(callback) && callback(error)
    }
  }
  const actionUpdateBuybackType = async(data, callback = () => {}) => {
    try{
      await store.dispatch('setting/buybackDevice/updateBuybackType', data)
      isFunction(callback) && callback()
    } catch(error) {
      console.log(error)
    }
  }
  const actionCategories = async (callback = () => {}) => {
    try {
      await store.dispatch('setting/buybackDevice/categories')
      isFunction(callback) && callback()  
    } catch (error) {
      console.log(error)
    }
  }
  const isComputer = (category) => {
    const categoryIndex = categories.value.length > 0 ? categories.value.map(category => category.name.toLowerCase()).findIndex((eachCategory) => eachCategory === category.toLowerCase()) : -1
    return categoryIndex !== -1 ? categories.value[categoryIndex].is_computer : 0
  }
  const actionCarriers = async () => {
    try {
      loading.carriers = true
      const response = await store.dispatch('setting/buybackDevice/getCarriers')
      carriers.value = response
    } finally {
      loading.carriers = false
    }
  }
  const actionUpdateCarriers = async(data, callback = () => {}) => {
    if (cancelToken.value) cancelToken.value.cancel()
    cancelToken.value = axios.CancelToken.source()

    loading.carriers = true
    try {
      const response = await store.dispatch('setting/buybackDevice/updateCarriers', {
        'data': JSON.stringify({ arr_networks: [...data] }),
        cancelToken: cancelToken.value && cancelToken.value.token
      })
      isFunction(callback) && callback(null)
      return response
    } catch (error) {
      isFunction(callback) && callback(error)
    } finally {
      loading.carriers = false
    }
  }
  const actionExportActiveCatalog = async () => {
    try {
      exportData.loading = true
      await store.dispatch('setting/buybackDevice/exportActiveProductCatalog', {
        data: {
          extension: exportData.format
        }
      })
    } catch (err) {
      console.error(`Error exporting active products catalog: `.err)
    } finally {
      exportData.loading = false
    }
  }

  return {
    state,
    stateAll,
    categoriesTab,
    brandsTab,
    data,
    exportData,
    loading,
    isComputer,
    actionAll,
    actionGetChildren,
    actionUpdate,
    actionUpdateBuybackType,
    actionCategories,
    actionCarriers,
    actionUpdateCarriers,
    actionExportActiveCatalog,
    cancelToken,
    carriers,
    tabBatchSize,
    currentTabOffset,
    onLoadMoreTabs,
    loadingTabBrand
  }
}

