const useGeneralSetting = () => {
  const data = reactive({
    currencies: null,
    countries: null,
    timezones: null,
    exp_days_pricelock: 0,
    buyback_number: {
      prefix_order_number_sequence: 'AS',
      zero_padding_order_number_sequence: 5,
      preview: 'AS00001'
    }
  })

  const loading = reactive({
    all: false,
    update: false
  })
  const loadingCountry = reactive({
    all: false,
    update: false
  })
  const loadingTimezone = reactive({
    all: false,
    update: false
  })
  const loadingPriceLock = reactive({
    all: false,
    update: false
  })
  const loadingBuybackPrefix = reactive({
    all: false,
    update: false
  })

  const setData = (payload = {}) => {
    data.currencies = payload
  }
  const setDataCountry = (payload = {}) => {
    data.countries = payload
  }
  const setDataTimezone = (payload = {}) =>{
    data.timezones = payload
  }
  const setDataPriceLock = (payload = 0) => {
    data.exp_days_pricelock = payload
  }
  const setDataBubyackPrefix = (payload) => {
    if(isObject(payload)){
      for(const [key, value] of Object.entries(payload)){
        if(Object.keys(data.buyback_number).includes(key)) data.buyback_number[key] = value
      }
    }
  }

  const actionUser = async () => {
    await store.dispatch('user/me')
  }

  const actionShow = async (callback = () => {}) => {
    loading.all = true
    await actionUser()
    const response = await store.dispatch(`setting/generalSettings/currencies`)
    setData(response)
    isFunction(callback) && callback()
    loading.all = false
  }
  const actionShowCountries = async (callback=()=>{}) => {
    loadingCountry.all = true
    const response = await store.dispatch('setting/generalSettings/countries')
    setDataCountry(response)
    isFunction(callback) && callback()
    loadingCountry.all = false
  }
  const actionShowTimezones = async(callback=()=>{}) => {
    loadingTimezone.all = true
    const response = await store.dispatch('setting/generalSettings/timezones')
    setDataTimezone(response)
    isFunction(callback) && callback()
    loadingTimezone.all = false
  }
  const actionShowPriceLock = async(callback=()=>{}) => {
    try{
      loadingPriceLock.all = true
      const response = await store.dispatch('setting/generalSettings/priceLock')
      setDataPriceLock(response.exp_days_pricelock)
      isFunction(callback) && callback()
    }finally{
      loadingPriceLock.all = false
    }
  }
  const actionShowBuybackPrefix = async(callback=()=>{}) => {
    try{
      loadingBuybackPrefix.all = true
      const response = await store.dispatch('setting/generalSettings/buybackPrefix')
      setDataBubyackPrefix(response)
      isFunction(callback) && callback()
    }finally{
      loadingBuybackPrefix.all = false
    }
  }
  const actionUpdatePriceLock = async(callback=()=>{}) => {
    const payload = {
      exp_days_pricelock: data.exp_days_pricelock
    }
    try {
      loadingPriceLock.update = true
      await store.dispatch(`setting/generalSettings/update`, payload)
      isFunction(callback) && callback()
    }
    finally {
      loadingPriceLock.update = false
    }
  }
  const actionUpdate = async (payload, callback = () => {}) => {
    try {
      loading.update = true
      loadingBuybackPrefix.update = true
      await store.dispatch(`setting/generalSettings/update`, payload)
      await actionUser()
      isFunction(callback) && callback()
    }
    finally {
      loading.update = false
      loadingBuybackPrefix.update = false
    }
  }

  const actionUpdateCountry = async(payload, callback = () => {}) => {
    try{
      loadingCountry.update = true
      await store.dispatch(`setting/generalSettings/update`, payload)
      await actionUser()
      isFunction(callback) && callback()
    }finally{
      loadingCountry.update = false
    }
  }
  const actionUpdateTimezone = async(payload, callback=()=>{}) => {
    try{
      loadingTimezone.update = true
      await store.dispatch(`setting/generalSettings/update`, payload)
      await actionUser()
      isFunction(callback) && callback()
    }finally{
      loadingTimezone.update = false
    }
  }
  const stateUpdate = computed(() => store.state.setting.generalSettings.currenciesUpdate)

  return {
    data,
    loading,
    loadingCountry,
    loadingTimezone,
    loadingPriceLock,
    loadingBuybackPrefix,
    stateUpdate,
    actionShow,
    actionShowCountries,
    actionShowTimezones,
    actionUpdate,
    actionUpdateCountry,
    actionUpdateTimezone,
    actionShowPriceLock,
    actionUpdatePriceLock,
    actionShowBuybackPrefix
  }
}

