const useMainMarketPlace = () => {
  const data = reactive({
    loading: {
      all: false,
      partner: false,
      marketplace: false
    },
    is_enable_partner: 0,
    is_enable_marketer: 0,
    is_marketplace_available: 0,
    total_partner: 0,
    total_market: 0
  })

  const getInfo = async (type = 'all') => {
    // type = 'marketplace' || 'partner' || 'all'
    data.loading[type] = true
    await store.dispatch('setting/getMarketPlaceSetting').then((res) => {
      data.is_marketplace_available = res.is_marketplace_available
      data.is_enable_marketer = res.marketer.is_enable
      data.total_partner = res.marketer.total_linked
      data.is_enable_partner = res.partner.is_enable
      data.total_market = res.partner.total_linked
      data.loading[type] = false
    }).catch((err) => {
      data.loading[type] = false
    })
  }
  const updateInfo = async (type = 'all') => {
    // type = 'marketplace' || 'partner' || 'all'
    data.loading[type] = true
    await store.dispatch('setting/updateMarketPlaceSetting', {
      is_enable_marketer: data.is_enable_marketer,
      is_enable_partner: data.is_enable_partner
    }).then(async (res) => {
      await getInfo(type)
    }).catch((err) => {
      data.loading[type] = false
    })
  }

  return {
    data,
    getInfo,
    updateInfo
  }
}

